@import "assets/vendor/slick/slick/slick.css";
@import "assets/vendor/slick/slick/slick-theme.css";
@import "assets/vendor/icons/feather.css";
@import "assets/vendor/bootstrap/css/bootstrap.min.css";
@import "assets/css/style.css";
@import "assets/vendor/sidebar/demo.css";
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css);


:root.pink{
    /* --primary : #ec0061;
    --primary1 : #d10258; */

    --primary : #ea0029;
    --primary1 : #db072e;
    --light : white;
    /* --primary : #fb4a6f;
    --primary1 : #fc2d5a; */

    

    /* --primary : #218380;
    --primary1 : #28A4A0; */
}
p {
    margin-top: 2px;
    /* margin-bottom: 1rem; */
}

.validate{
    border: 1px solid rgb(248, 77, 77);
}

.checked{
    width: 100%;
    height: 100%;
    margin: 0;
    background: no-repeat;
    border-radius: 0.4rem !important;
    /* pointer-events: none; */
    position: absolute;
    border-color: var(--primary);
    border: 2px solid var(--primary);
    display: block;
    border-radius: 0.375rem;
}

.form-check-input:checked {
    background-color: var(--primary);
    border-color: var(--primary);
}

.form-check-input:focus {
    border-color: var(--primary);
    outline: 0;
    box-shadow: 0 0 0 0.25rem rgba(217, 38, 98, 0.25);
}



.success{
    background: var(--primary);
    font-size: 35px;
    color: white;
    padding: 11px;
    border-radius: 50%;
}

.category{
    border: 2px solid var(--primary);
    padding: 4px;
    border-radius: 50%;
}

div:where(.swal2-container) button:where(.swal2-styled).swal2-confirm {
    border: 0;
    border-radius: 0.25em;
    background: initial;
    background-color: var(--primary);
    color: #fff;
    font-size: 1em;
}

div:where(.swal2-container) button:where(.swal2-styled).swal2-confirm {
    border: 0;
    border-radius: 0.25em;
    background: initial;
    background-color: var(--primary)!important;
    color: #fff;
    font-size: 1em;
}

div:where(.swal2-container) div:where(.swal2-actions):not(.swal2-loading) .swal2-styled:hover {
    background-image: linear-gradient(var(--primary), var(--primary1))!important;
}

div:where(.swal2-container) button:where(.swal2-styled).swal2-confirm:focus {
    box-shadow: 0 0 0 3px rgb(255 255 255 / 50%) !important;
}


.accordion-button:not(.collapsed) {
    color: var(--primary);
    background-color: #e2032b00;
    box-shadow: inset 0 calc(-1 * var(--bs-accordion-border-width)) 0 #e3042c0d;
}

.accordion-button:focus {
    z-index: 3;
    border-color: none;
    outline: 0;
    box-shadow: none;
}

.text-container {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%; /* Default style for larger screens */

  }

  @media (min-width:375px) and  (max-width: 1180px) {
    .text-container {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 180px; /* Default style for larger screens */
      }
  }

  .cart i{
    color: var(--primary);
    font-size: 75px;
  }

  .dropdown-item.active, .dropdown-item:active {
    color: var(--light);
    text-decoration: none;
    background-color: var(--primary);
}